  filter: grayscale(100%);

}
*/
.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  filter: grayscale(100%);
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f0f0;
}

.image-container {
  width: 100%;
  max-width: 600px;
  /* Set the max width as per your design */
  height: 400px;
  /* Set the height as per your design */
  overflow: hidden;
  position: relative;
  border: 1px solid #ccc;
}

#scrollable-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}